export default "*, *:before, *:after {\n  box-sizing: border-box;\n  outline: none;\n}\n\nbody {\n  background: #fd8215;\n  font: 14px/1 \"Open Sans\", helvetica, sans-serif;\n  -webkit-font-smoothing: antialiased;\n}\n\n.box {\n  height: 100%;\n  width: 100%;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  background: #fd8215;\n  border-radius: 100%;\n  overflow: hidden;\n}\n\n.box .percent {\n  position: absolute;\n  left: 0;\n  top: 0;\n  z-index: 3;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  display: -webkit-flex;\n  align-items: center;\n  justify-content: center;\n  color: #fff;\n  font-size: 64px;\n}\n\n.box .water {\n  position: absolute;\n  left: 0;\n  top: 0;\n  z-index: 2;\n  width: 100%;\n  height: 100%;\n  transform: translate(0, 100%);\n  background: #fdb97d;\n  transition: all 0.3s;\n}\n\n.box .water_wave {\n  width: 200%;\n  position: absolute;\n  bottom: 100%;\n}\n\n.box .water_wave_back {\n  right: 0;\n  fill: #ff9b43;\n  -webkit-animation: wave-back 4s infinite linear;\n          animation: wave-back 4s infinite linear;\n}\n\n.box .water_wave_front {\n  left: 0;\n  fill: #fdb97d;\n  margin-bottom: -1px;\n  -webkit-animation: wave-front 1.8s infinite linear;\n          animation: wave-front 1.8s infinite linear;\n}\n\n@-webkit-keyframes wave-front {\n  100% {\n    transform: translate(-50%, 0);\n  }\n}\n\n@keyframes wave-front {\n  100% {\n    transform: translate(-50%, 0);\n  }\n}\n\n@-webkit-keyframes wave-back {\n  100% {\n    transform: translate(50%, 0);\n  }\n}\n\n@keyframes wave-back {\n  100% {\n    transform: translate(50%, 0);\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */";